body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(168, 198, 168);
  font-size: 34px;
  color: rgb(40, 40, 40);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input{
  width: 255px;
  border: none;
  background-color:#8ac18d;
  border-radius: 5px;
}

input::placeholder{

  color: rgb(40, 40, 40);
}


.filter-list-container {
  margin:0 auto;
  margin-left: 40%;
  text-align: left;
  padding: 20px;
  border-radius: 10px;
}

.filter-controls {
  display: flex;
  align-items: center; 
  gap: 10px; 
}

.dropdown-toggle, .clearB {
  background-color: #8ac18d;
  color: rgb(40, 40, 40);
  font-size: 16px;
  border: none;
  &:hover, &:focus {
      background-color: #45a049;
      color: white;
  }
}

.dropdown-toggle{
  position:absolute;
  margin-top: -14.8px;
  height: 33px;
}

.dropdown-menu {
  background-color: #8ac18d;
  position: absolute; /* Add this line */
  left: 0; /* Align with the left edge of the dropdown toggle */
  top: 100%; /* Position below the toggle */
  z-index: 1000; /* Ensure it's above other content */
}

.dropdown-item {
  display: block;
  width: 100%;
}

.clearB{
  border-radius: 5px;
  padding: 5px;
  width: 75px;
  margin-top: 3px;
  margin-left: 170px
}
